<template>
  <div id="app" class="app">
    <div class="text">
      <pre>{{`  ${currentSonnetId}`}}</pre>
      <pre>{{currentSonnetText}}</pre>
    </div>
    <div class="controls">
      <button class="btn btn-push" v-on:click="getRandomSonnet">push me</button>
      <div class="controls__toggle">
        <button class="btn btn-toggle" v-on:click="getCurrenLangArray">toggle lang</button>
        <pre>{{currentSonnetsArr[0].lang}}</pre>
      </div>
    </div>
  </div>
</template>

<script>
  import sonnets from './data/sonnets.js'

  export default {
    name: 'app',
    methods: {
      findDomElements: function() {
        this.btnToggle = document.querySelector('.btn-toggle');
      },
      getRandomArrayNum: function(array) {
        return Math.round(Math.random() * (array.length - 1) + 1)
      },
      getEngSonnetsArr: function() {
        // this.btnToggle.classList.remove('btn-toggle--rus');
        // this.btnToggle.classList.add('btn-toggle--eng');
        this.currentSonnetsArr = this.sonnets.map(function(sonnet) {
          return {
            lang: 'eng',
            id: sonnet.id,
            sonnetText: sonnet.eng
          };
        })
      },
      getRusSonnetsArr: function() {
        // this.btnToggle.classList.remove('btn-toggle--eng');
        // this.btnToggle.classList.add('btn-toggle--rus');
        this.currentSonnetsArr = this.sonnets.map(function(sonnet) {
          return {
            lang: 'rus',
            id: sonnet.id,
            sonnetText: sonnet.rus
          };
        })
      },
      getCurrenLangArray: function() {
        if (this.currentSonnetsArr[0] !== undefined) {
          if (this.currentSonnetsArr[0].lang === 'eng') {
            this.getRusSonnetsArr();
          } else {
            this.getEngSonnetsArr();
          }
        } else {
          this.getEngSonnetsArr();
        }
      },
      getRandomSonnet: function() {

        const currentSonnet = this.currentSonnetsArr[this.getRandomArrayNum(this.currentSonnetsArr)];
        this.currentSonnetText = currentSonnet.sonnetText;
        this.currentSonnetId = currentSonnet.id;
      }
    },
    beforeMount() {
      // this.findDomElements(),
        this.getEngSonnetsArr()
    },
    data() {
      return {
        sonnets: sonnets,
        currentSonnetText: '\nShakespeare\'s sonnets',
        currentSonnetId: ' ',
        currentSonnetsArr: ' ',
        btnToggle: ''
      }
    }
  }
</script>

<style lang="scss">
  // fonts
  @font-face {
    font-family: 'ArsenalRegular';
    src: url('./assets/fonts/ArsenalRegular/ArsenalRegular.eot');
    src: url('./assets/fonts/ArsenalRegular/ArsenalRegular.woff') format('woff'), url('./assets/fonts/ArsenalRegular/ArsenalRegular.ttf') format('truetype');
    font-style: normal;
    font-weight: 400;
  }

  @font-face {
    font-family: 'CircularStdBook';
    src: url('./assets/fonts/CircularStd-Book/CircularStd-Book.eot');
    src: url('./assets/fonts/CircularStd-Book/CircularStd-Book.eot?#iefix') format('embedded-opentype'), url('./assets/fonts/CircularStd-Book/CircularStd-Book.woff') format('woff'), url('./assets/fonts/CircularStd-Book/CircularStd-Book.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }

  @font-face {
    font-family: 'CircularStdBold';
    src: url('./assets/fonts/CircularStd-Bold/CircularStd-Bold.eot');
    src: url('./assets/fonts/CircularStd-Bold/CircularStd-Bold.eot?#iefix') format('embedded-opentype'), url('./assets/fonts/CircularStd-Bold/CircularStd-Bold.woff') format('woff'), url('./assets/fonts/CircularStd-Bold/CircularStd-Bold.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }

  @font-face {
    font-family: 'CircularStdBlack';
    src: url('./assets/fonts/CircularStd-Black/CircularStd-Black.eot');
    src: url('./assets/fonts/CircularStd-Black/CircularStd-Black.eot?#iefix') format('embedded-opentype'), url('./assets/fonts/CircularStd-Black/CircularStd-Black.woff') format('woff'), url('./assets/fonts/CircularStd-Black/CircularStd-Black.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }

  // colors
  $lightgray: #bfbfb9;
  $superlightblue: #cdd8e4;
  $lightblue: #a0afc7;
  $blue: #7d88a3;
  $blue1: #7d88d3;
  $darkblue: #474553;
  *:not(body),
  *::before,
  *::after {
    box-sizing: border-box;
    transition: all .4s cubic-bezier(.25, .8, .25, 1);
  }

  html {
    font-size: 100%;
  }

  body {
    height: 100%;
    font-size: 12px;
    font-family: 'ArsenalRegular';
    display: flex;
    // font-family: 'Varela Round', sans-serif;
    line-height: 1.42em;
    color: #3e3f43;
    margin: 0;
  }

  #app {
    min-width: 900px;
    margin: 50px auto;
    display: flex;
    justify-content: space-between;
    & pre {
      font-family: 'ArsenalRegular';
      font-size: 22px;
      line-height: 24px;
      transition: all 2s cubic-bezier(0.25, 0.8, 0.25, 1);
      &::selection {
        background-color: $superlightblue;
      }
    }
    & button {
      font-family: 'ArsenalRegular';
    }
  }

  .btn {
    outline-color: $lightblue;
    border: none;
    background-color: $darkblue;
    color: #fff;
    padding: 10px 30px;
    border-radius: 20px;
    font-family: 'ArsenalRegular';
    font-size: 18px;
    transition: background-color 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
    cursor: pointer;
    &:hover {
      background-color: $blue;
    }
    &-toggle {
      padding: 10px 20px;
      border: 2px solid $darkblue;
      // font-size: 0;
      // background-color: transparent;
      position: relative;
      &:hover {
        background-color: inherit;
      }
      &--eng {
        background-color: orange;
        &::before {
          content: "";
          position: absolute;
          display: block;
          width: 18px;
          height: 18px;
          top: 1px;
          left: 1px;
          background-color: $blue;
          border-radius: 50%;
        }
        &--rus {
          background-color: skyblue;
          &::before {
            content: "";
            position: absolute;
            display: block;
            width: 18px;
            height: 18px;
            top: 1px;
            right: 1px;
            background-color: $blue;
            border-radius: 50%;
          }
        }
      }
    }
  }

  .controls {
    &__toggle {
      margin-top: 20px;
    }
  }
</style>



